/*---------------ACTIVITY ICONS---------------*/

.activity-icons{
  margin-bottom: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
}

.activity-icons img{
  width: 100%;
}

.activity-icons ul{
  margin: 0;
  padding: 0;
  list-style: none;
}
.activity-icons li{
  display: inline-block;
  margin-right: 6px;
}
.activity-icons .ico {
  width: 21px;
  position: relative;
  cursor: pointer;
}

.activity-icons .ico p{
  position: absolute;
  background: #f5f2f2;
  color: #656363;
  left: 0;
  width: 123px;
  margin-left: -51px;
  text-align: center;
  padding: 2px 15px;
  border: 1px solid #d0cbcb;
  border-radius: 6px;
  font-size: 12px;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  bottom: 60px;
}

.activity-icons .ico:hover p{
  opacity: 1;
  pointer-events: auto;
  bottom: 30px;
  visibility: visible;
}

.activity-icons .ico p span{
  position: absolute;
  color: #f5f2f2;
  text-shadow: 0px 1px 0px #d0cbcb;
  left: 0;
  right: 0;
  top: auto;
  bottom: -14px;
  font-size: 20px;
}


@media (max-width: 850px){
  .activity-icons .ico p span{
    font-size: 15px;
    bottom: -11px;
  }
  .activity-icons li:nth-child(1) p{
    left: 42px;
  }
  .activity-icons li:nth-child(1) p span{
    left: -83px;
  }
  .activity-icons li:nth-child(2) p{
    left: 17px;
  }
  .activity-icons li:nth-child(2) p span{
    left: -38px;
  }
}

/*---------------END ACTIVITY ICONS---------------*/



/*---------------Filters---------------*/

.filters{
  padding: 11px 0;
  width: 60%;
}

.filters-group{
  padding: 11px 0 11px 11px;
  position: relative;
}

.filters-group__content{
  position: relative;
}
.filters-group__content:after{
  content: '';
  position: absolute;
  right: -11px;
  top: 0;
  height: 100%;
  width: 1px;
  background: #e8e8e8;
}

.filters-group-first-child{
  padding-left: 0;
}
.filters-group-first-child:before{
  display: none;
}
.filter-group-range{
  padding-bottom: 8px;
}

.filters-group strong{
  margin-bottom: 8px;
  font-size: 16px;
  display: block;
}

.filters-group label{
  vertical-align: middle;
  margin: 0;
  font-weight: 400;
}

.filter-item{
  margin-bottom: 3px;
  position: relative;
}

.filter-item img{
  position: absolute;
  top: 5px;
  right: 85px;
  width: 13px;
}


.custom-checkbox{
  display: inline-block;
  vertical-align: middle;
  width: 14px;
  height: 14px;
  border: 1px solid #d9d9d9;
  border-radius: 2px;
  margin-right: 2px;
  position: relative;
}

.custom-checkbox input[type=checkbox]{
  position: absolute;
  height: 100%;
  width: 100%;
  opacity: 0;
  margin: 0;
  z-index: 1;
}

.custom-checkbox-mark{
  position: absolute;
  height: 5px;
  width: 12px;
  top: 50%;
  left: 43%;
  border: solid #00a651;
  border-width: 0 0 2px 2px;
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
  display: none;
  margin: -4px 0 0 -5px;
  pointer-events: none;
}

.custom-checkbox input[type=checkbox]:checked + .custom-checkbox-mark{
  display: block;
}

/*JQuery UI input range*/

.ui-slider{
  background: #e1e1e1;
  height: 4px;
  border-radius: 0;
  border: none;
  position: relative;
  touch-action: pan-y;
}

.ui-slider-range{
  position: absolute;
  z-index: 1;
  top: 0;
  background: #00a651;
  height: 4px;
}

.ui-slider-handle{
  position: absolute;
  border-radius: 19px;
  width: 11px;
  height: 11px;
  border: 2px solid rgb(0, 166, 81);
  background: rgb(255, 255, 255);
  z-index: 2;
  top: -.3em;
  margin-left: -.6em;
}
.ui-slider-handle:before{
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  width: 3px;
  height: 3px;
  background: #00a651;
  border-radius: 50%;
}
.ui-slider-handle:focus{
  outline: none;
}

.slider-range-output{
  padding: 0;
  border: none;
  margin-bottom: 13px;
}


/*END JQuery UI input range*/

