.loader{
  position: relative;
  padding: 100px 0;
}

.loader__inside:before{
  content: "";
  position: absolute;
  top: 0;
  left: -25px;
  height: 12px;
  width: 12px;
  border-radius: 12px;
  -webkit-animation: loader__inside-g 3s ease-in-out infinite;
  animation: loader__inside-g 3s ease-in-out infinite;
  background-color: rgba(0, 56, 86, .2);
}

.loader__inside{
  position: relative;
  width: 12px;
  height: 12px;
  top: 46%;
  left: 46%;
  border-radius: 12px;
  -webkit-animation: loader__inside-m 3s ease-in-out infinite;
  animation: loader__inside-m 3s ease-in-out infinite;
  background-color: rgba(0, 56, 86, .2);
}


.loader__inside:after{
  content: "";
  position: absolute;
  top: 0;
  left: 25px;
  height: 10px;
  width: 10px;
  border-radius: 10px;
  -webkit-animation: loader__inside-d 3s ease-in-out infinite;
  animation: loader__inside-d 3s ease-in-out infinite;
  background-color: rgba(0, 56, 86, .2);
}

@-webkit-keyframes loader__inside-g{
  0%{background-color: rgba(0, 56, 86, .2);}
  25%{background-color: rgba(0, 56, 86, 1);}
  50%{background-color: rgba(0, 56, 86, .2);}
  75%{background-color: rgba(0, 56, 86, .2);}
  100%{background-color: rgba(0, 56, 86, .2);}
}
@keyframes loader__inside-g{
  0%{background-color: rgba(0, 56, 86, .2);}
  25%{background-color: rgba(0, 56, 86, 1);}
  50%{background-color: rgba(0, 56, 86, .2);}
  75%{background-color: rgba(0, 56, 86, .2);}
  100%{background-color: rgba(0, 56, 86, .2);}
}

@-webkit-keyframes loader__inside-m{
  0%{background-color: rgba(0, 56, 86, .2);}
  25%{background-color: rgba(0, 56, 86, .2);}
  50%{background-color: rgba(0, 56, 86, 1);}
  75%{background-color: rgba(0, 56, 86, .2);}
  100%{background-color: rgba(0, 56, 86, .2);}
}
@keyframes loader__inside-m{
  0%{background-color: rgba(0, 56, 86, .2);}
  25%{background-color: rgba(0, 56, 86, .2);}
  50%{background-color: rgba(0, 56, 86, 1);}
  75%{background-color: rgba(0, 56, 86, .2);}
  100%{background-color: rgba(0, 56, 86, .2);}
}

@-webkit-keyframes loader__inside-d{
  0%{background-color: rgba(0, 56, 86, .2);}
  25%{background-color: rgba(0, 56, 86, .2);}
  50%{background-color: rgba(0, 56, 86, .2);}
  75%{background-color: rgba(0, 56, 86, 1);}
