/*---------------Most visited activities section---------------*/

.visited-activities-section{
  background: #F2F2F2;
  padding: 34px 0 58px;
  &__header{
    color: #535353;
    margin-bottom: 51px;
  }
  &__title{
    font-size: 38px;
    font-weight: 700;
    margin: 0 0 10px;
    line-height: 1;
  }
  &__sub-title{
    font-size: 15px;
    margin: 0;
    line-height: 1;
  }
}

.top-activities{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  &__link,
  &__link:hover,
  &__link:focus{
    color: #000;
  }
  &__item{
    width: calc( (100%/4) - 15px );
  }
  &__image{
    height: 93px;
    object-fit: cover;
    -o-object-fit: cover;
    width: 100%;
  }
  &__info{
    padding: 0 18px;
  }
  &__name{
    margin: 14px 0 6px;
    font-size: 20px;
    line-height: 1;
    font-weight: 700;
  }
  &__description{
    margin: 0 0 15px;
  }
  &__sub{
    bottom: 0;
  }
  &__price{
    font-size: 20px;
    color: #FDB600;
    margin-left: 7px;
  }
}


@media screen and (max-width: 991px){

  .top-activities__item{
    width: calc( (100%/2) - 15px );
    margin-bottom: 30px;
  }

}

@media screen and (max-width: 640px){

  .top-activities__info{
    padding: 0;
  }

}

@media screen and (max-width: 500px){

  .visited-activities-section__title{
    font-size: 30px;
  }

  .top-activities{
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
  }

  .top-activities__item{
    width: 100%;
    max-width: 370px;
  }

  .top-activities__image{
    height: 173px;
  }

}

/*---------------END Most visited activities section---------------*/